<template>
	<view class="page" :style="{ height: `${sysHeight}px`, width: `${sysWidth}px` }">
		<view class="titleView" :style="{ bottom: `${40}px` }">
			{{pageIndex}}/{{totalWord}}
		</view>
	<!-- 	<view>
			<button @click="deleteWord()"  style="width: 100rpx;height: 100rpx;color: #FFFFFF;background-color: #DD001C;" type="default">删除</button>
				<button @tap="updateWordType(0)" style="width: 80rpx;height: 100rpx;color: #FFFFFF;background-color: #007AFF;" type="default">动物</button>
				<button @tap="updateWordType(1)" style="width: 80rpx;height: 100rpx;color: #FFFFFF;background-color: #F0AD4E;" type="default">食物</button>
				<button @tap="updateWordType(2)" style="width: 80rpx;height: 100rpx;color: #FFFFFF;background-color: #3F536E;" type="default">日常</button>
		</view> -->
		<view
			v-for="(item, index) in dataList"
			class="move-view"
			:key="item._id"
			@touchend="touchend"
			@tap="tapCard(item)"
			@touchmove="touchMove"
			@touchstart="touchStart"
			:animation="animationData[index]"
			:style="{
				transform:
					index < number
						? `rotate(${rotate * index}deg) scale(${1 - (1 - scale.x) * index},${1 - (1 - scale.y) * index}) skew(${skew.x * index}deg, ${skew.y *
								index}deg) translate(${translate.x * index}px, ${translate.y * index}px)`
						: `rotate(${rotate * (number - 1)}deg) scale(${1 - (1 - scale.x) * (number - 1)},${1 - (1 - scale.y) * (number - 1)}) skew(${skew.x *
								(number - 1)}deg, ${skew.y * (number - 1)}deg) translate(${translate.x * (number - 1)}px, ${translate.y * (number - 1)}px)`,
				zIndex: `${99999 - item._id}`,
				opacity: index < number ? `${1 - (1 - opacity) * index}` : `${1 - (1 - opacity) * (number - 1)}`
			}"
		>
			<view class="cardBox"><card-box :thumbnail="`${apiHost}/api/files/${item.collectionId}/${item.id}/${item.thumbnail}`" :bgColor="item.bgColor"  :en="item.en" :zh="item.zh" ref="cardBox"></card-box></view>
	
		</view>
		<!-- <view class="actionBtnBgView" >
			<image class="actionImg" src="../../static/common/ic_btn_cn~iphone.png" mode="aspectFit"></image>
			<image class="actionImg" src="../../static/common/ic_btn_en~iphone.png" mode="aspectFit"></image>
		</view> -->
		
		</view>
	</view>
</template>

<script>
import clCardDel from '@/components/cl-cardDel/cl-cardDel';
import cardBox from './card-box';
import PocketBase from 'pocketbase';


// 在页面中定义插屏广告
let interstitialAd = null

export default {
	mixins: [clCardDel],
	components: { cardBox },
	data() {
		return {
			words:[],
			currentShowWordInfo:null,
			pageIndex:1,
			totalWord:0,
			bgViewColors : [
				'#198AFA',
				'#E86280',
				'#6F7EA8',
				'#327560',
				'#6F8FAB',
				'#768D9C',
				'#9599A1',
				'#F4B78C',
				'#F5AD44',
				'#2496D9',
				'#758640',
				'#CD3C4A',
				'#278F74',
				'#6E6E71',
				'#3E9DD6',
				'#E66466',
				'#D5B383'
			],
			dataGroup:[],
			originWords:[],
			apiHost:'http://127.0.0.1:8090'
		};
	},
	async onLoad(options) {
		console.log(options)
		uni.setNavigationBarTitle({
			title:options.title
		})
		// const eventChannel = this.getOpenerEventChannel();
		  // 监听acceptDataFromOpenerPage事件，获取上一页面通过eventChannel传送到当前页面的数据
		// eventChannel.on('acceptDataFromOpenerPage', function(data) {
		//     console.log(data)
		// 	options.words = data.words;
		// 	console.log(options);
		// })
		var wordList = await this.getWordsByCat("AA")
		console.log(wordList.items[0].en)
		
		this.totalWord = wordList.totalItems
		this.dataList = wordList.items;
		console.log(options.cat)
		console.log("onload---------------------------")
		// var words = JSON.parse(decodeURIComponent(words));
		
		this.originWords = wordList.items;
		console.log(this.originWords)
		
		
		this.dataGroup = wordList.items;
		
		// 在页面onLoad回调事件中创建插屏广告实例
		// if (wx.createInterstitialAd) {
		//   interstitialAd = wx.createInterstitialAd({
		//     adUnitId: 'adunit-057d2daa76faf2fa'
		//   })
		//   interstitialAd.onLoad(() => {})
		//   interstitialAd.onError((err) => {})
		//   interstitialAd.onClose(() => {})
		// }
		
		setTimeout(() => {
			// 在适合的场景显示插屏广告
			if (interstitialAd) {
			  interstitialAd.show().catch((err) => {
			    console.error(err)
			  })
			}
		}, 8000);

	},
	mounted() {
	
	},
	methods: {
		async getWordsByCat(cat){
			if (cat== undefined){
				cat = "animal"
			}
			const pb = new PocketBase(this.apiHost);
			const wordList = await pb.collection('word').getList(1, 50, {
				filter: `cat="${cat}"`,
			});
			return wordList
		},
		deleteWord(){
		console.log("deleteWord=====")
		uni.showLoading({
			
		})
			var that = this
			uniCloud.callFunction({
			name:'deleteWord',
			data:{
					"enWord":this.currentShowWordInfo.enTitle,
					"zhWord":this.currentShowWordInfo.zhTitle
			},
			success:(res)=>{
				uni.hideLoading()
				
				uni.showToast({
					title:res.result.data
				})
				console.log(res);
				console.log('---1--2-3');
			}
			})
		},
		updateWordType(type){
			console.log("updateWordType=====")
			console.log(this.currentShowWordInfo)
			var that = this
			uni.showLoading({
				
			})
			uniCloud.callFunction({
			name:'updateWordInfo',
			data:{
					"enWord":this.currentShowWordInfo.enTitle,
					"zhWord":this.currentShowWordInfo.zhTitle,
					"type":type
			},
			success:(res)=>{
				uni.hideLoading()
				console.log(res);
				console.log('---1--2-3');
				uni.showToast({
					title:res.result.data
				})
			}
			})
		},
		tapLove() {
			if (this.dataList.length == 0) return;
			this.moveX = 10; //设置角度y为0水平
			this.moveY = 1;
			this._del();
		},
		tapLoathe() {
			if (this.dataList.length == 0) return;
			this.moveX = -10; //设置角度
			this.moveY = 1;
			this._del();
		},
		//设置初始参数
		init() {
			this.number = 0; //card 3
			this.translate = { x: 0, y: 8 }; //y下移10px
			this.scale = { x: 0.95, y: 1 }; //x 缩小0.9
			this.type = true;

			this.moveRotate = {
				//设置位移图片旋转角度距离  card中心点 - 指向坐标
				x: 0,
				y: uni.getSystemInfoSync().screenHeight
			};
		},
		//获取数据
		getData() {
			var that = this
			let promise = new Promise((resolve, reject) => {
				console.log("---getData---")
				
				that.dataList = that.dataGroup.splice(0,10)
				that.currentShowWordInfo = that.dataList[0]
				// that.ScanAudio(that.currentShowWordInfo.voiceURL);
				resolve();
			});
			return promise;
		},
		//触摸中判断
		moveJudge(x, y, ratio) {
			let el = this.$refs.cardBox[0];

			if (x > 20) {
				el.moveRight(ratio);
			} else if (x < -20) {
				el.moveLeft(ratio);
			} else {
				el.moveCenter();
			}
		},
		//触摸结束判断
		endJudge(x, y) {
			let el = this.$refs.cardBox[0];
			if (Math.abs(x) < 40) {
				this._back();
				el._back();
			} else {
				this._del();
				el.clearAnimation();
			}
		},
		//删除card时
		delCard(x, y) {
			if (x > 0) {
				console.log(this.dataList[0], '喜欢');
			} else {
				console.log(this.dataList[0], '不喜欢');
			}
			// if(this.dataList.length == 1){
			// 	return
			// }
	
			if(this.pageIndex == this.totalWord){
				this.ScanAudio('/static/voice/sound_unbelievable.mp3');
				var that = this
				uni.showModal({
			          content: '已经学完，是否再来一遍？',
			          showCancel: true,
			          buttonText: '确定',
			          success: (res) => {
			            if (res.confirm) {
						that.pageIndex = 1
			              console.log('用户点击确定');
						  
						  // that.dataGroup = JSON.parse(decodeURIComponent(that.originWords));
						  that.dataGroup = that.originWords;
						  console.log(that.originWords)
						  console.log(that.dataGroup)
						  that.currentShowWordInfo = that.dataGroup[0]
						  that.ScanAudio(that.currentShowWordInfo.voice);
						  that.getData()
						  
			            } else if (res.cancel) {
			              console.log('用户点击取消');
						  uni.navigateBack({
						  	
						  })
			            }
			          }
			        })
			}else{
				this.pageIndex += 1
			}
			
			console.log(this.dataList)
			if(this.dataList.length > 1){
				this.currentShowWordInfo = this.dataList[1]
				this.ScanAudio(this.currentShowWordInfo.voice);	
			}			
		},
		tapCard(item) {
			console.log(item, '点击');
			
			this.ScanAudio(`${this.apiHost}/api/files/${item.collectionId}/${item.id}/${item.voice_slow}`);			
		}
		
	}
};
</script>

<style lang="scss" scoped>
